<template>
	<div class="dingMi-head">
		<img class="backBtn" src="../../../static/img/back.png" alt="" @click="goBack();">
		<span>钉钉3.4版本新功能介绍</span>
	</div>
</template>
<script>
	export default{
		methods: {
			goBack: function(){
				window.history.back();
			}
		}
	}
</script>
<style lang='scss' scoped>
	.dingMi-head{
			font-size: 16px;
			height:50px;
			padding-left:4%;
			box-sizing:border-box;
			border-bottom:1px solid #DADADE;
			.backBtn{
				height:15px;
				margin-top:15px;
			}
			span{
				display:inline-block;
				height:30px;
				border-left:1px solid #DADADE;
				margin:10px 0 0 13px;
				text-indent:13px;
				line-height:28px;
				color:#545759;
			}

		}
</style>



